import ICEBoundingBox from '../ICEBoundingBox';
import ICEPath from '../ICEPath';
import ICEVector from '../ICEVector';
/**
 * @class ICEIsogon 
 * 
 * @abstract
 * @author <damoqiongqiu@126.com>
 */
class ICEIsogon extends ICEPath {
    /**
     * radius, edges  AnimationManager 
     * @param props
     */
    constructor(props = { radius: 10, edges: 3 }) {
        super(props);
        //
        this.radius = 10;
        // N 
        this.edges = 3;
        //
        this.center = new ICEVector();
        this.radius = this.props.radius;
        this.edges = this.props.edges;
        this.center.x = this.radius + this.props.left;
        this.center.y = this.radius + this.props.top;
        this.state = { ...this.state, center: this.center };
    }
    /**
     * @overwrite
     * @param newState
     */
    setState(newState) {
        super.setState(newState);
        this.center.x = this.state.radius + this.state.left;
        this.center.y = this.state.radius + this.state.top;
    }
    /**
     *  N 
     * @returns
     */
    calcDots() {
        this.dots = [];
        let avgAngle = (2 * Math.PI) / this.state.edges;
        for (let i = 0; i < this.state.edges; i++) {
            let currentAngel = avgAngle * i;
            let x = Math.floor(this.state.radius * Math.cos(currentAngel) + this.state.radius);
            let y = Math.floor(this.state.radius * Math.sin(currentAngel) + this.state.radius);
            this.dots.push(new ICEVector({ x, y }));
        }
        return this.dots;
    }
    getMinBoundingBox() {
        const temp = new ICEBoundingBox({ left: 0, top: 0, width: 2 * this.state.radius, height: 2 * this.state.radius }); //width, height 
        const mat = this.state.transformMatrix;
        return temp.transform(mat);
    }
}
export default ICEIsogon;
