class ICEVector {
    constructor(v = { x: 0, y: 0 }) {
        this.x = 0;
        this.y = 0;
        this.x = v.x;
        this.y = v.y;
    }
    /**
     * 
     * @param v
     * @returns
     */
    add(v) {
        let newVector = new ICEVector();
        newVector.x = this.x + v.x;
        newVector.y = this.y + v.y;
        return newVector;
    }
    /**
     * 
     * @param v
     * @returns
     */
    sub(v) {
        let newVector = new ICEVector();
        newVector.x = this.x - v.x;
        newVector.y = this.y - v.y;
        return newVector;
    }
    mul(v) {
        //TODO:
        return this;
    }
    dot(v) {
        //TODO:
        return this;
    }
}
export default ICEVector;
